body {
  background-color: #FEFFD4;
  font-family: 'BioRhyme' ,'Arvo', serif;
}
img {
  display: block;
}

.header {
  position: fixed;
  z-index: 9;
  width: 100%;
  height: 80px;
  background-color: #000;
  border-bottom: 1px solid #FEFFD4;
  display: flex;
  align-content: center;
  .container {
    display: flex;
    padding: 0 80px;
    height: 100%;
    .logo {
      height: 100%;
      display: flex;
      align-items: center;
      color: #FEFFD4;
      font-size: 25px;
      text-decoration: none;
      font-family: 'BioRhyme' ,'Arvo', serif;
      margin-right: 10px;
      text-align: center;
      user-select:none;
    }
    .nav {
      color: #FEFFD4;
      display: flex;
      align-items: center;
      &__search, &__about{
        font-family: 'BioRhyme' ,'Arvo', serif;
        width: 95px;
        height: 35px;
        border: 1px solid #FEFFD4;
        border-radius: 20px;
        margin-left:20px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        user-select:none;
      }
      &__search:hover, &__about:hover{
        background-color: #FEFFD4;
        color: #000;
      }
    } 
  }
}

.intro {
  .container {
    width: 100%;
    height: 100vh;
    background-color: #000;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .title {
      padding-top: 180px;
      font-size: 100px;
      line-height: -1;
      color: #FEFFD4;
      text-align: center;
      font-family: 'BioRhyme' ,'Arvo', serif;
      user-select:none;
    }
    .sub-title {
      position: absolute;
      width: 100%;
      height: 100vh;
      user-select:none;
      .left {
        position: absolute;
        top: 53%;
        left: -40px;
        text-align: center;
        font-size: 25px;
        color: #FEFFD4;
        font-family: 'BioRhyme' ,'Arvo', serif;
        rotate: -90deg;
        user-select:none;
      }
      .right {
        position: absolute;
        top: 53%;
        right: -40px;
        text-align: center;
        font-size: 25px;
        color: #FEFFD4;
        font-family: 'BioRhyme' ,'Arvo', serif;
        rotate: 90deg;
        user-select:none;
      }
    }
    .desc {
      width: 100%;
      text-align: center;
      padding-bottom: 20px;
      font-size: 16px;
      line-height: 1.6;
      color: #FEFFD4;
      font-family: 'BioRhyme' ,'Arvo', serif;
      user-select:none;
    }
    .arrow {
      width: 100%;
      height: auto;
      display: flex;
      justify-content: center;
      padding-top: 100px;
      padding-bottom: 30px;
      align-content: flex-end;
      user-select:none;
      &__btn {
        width: 250px;
        height: 180px;
        border-radius: 90px;
        border: 2px solid #FEFFD4;
        background-color: transparent;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        z-index: 1;
      }
      &__btn:hover {
        background-color: #FEFFD4;
        img {
          filter: invert(100%);
          user-select:none;
        }
      }
    }
  }
}

.main {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  height: auto;
  min-height: 100vh;
  .search-container {
    width: 90%;
    padding-top: 135px;
    display: flex;
    justify-content: space-between;
    position: relative;
    .title {
      margin-left: 20px;
      font-size: 32px;
      font-family: 'BioRhyme' ,'Arvo', serif;
      padding-top: 8px;
      user-select:none;
    }
    .search-wrap {
      display: flex;
      .year-select {
        -webkit-appearance:none;
        width: 100px;
        height: 50px;
        font-size: 16px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        color: #000;
        margin-right: 10px;
        padding: 0 10px;
        box-sizing: border-box;
        background-color: transparent;
        outline: none;
        box-shadow: none;
        border: none;
        border: 3px solid #000;
        background-size: 10px;
        .year-option {
          background-color: #FEFFD4;
        }
      }
      .type-select {
        -webkit-appearance:none;
        width: 130px;
        height: 50px;
        font-size: 16px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        color: #000;
        margin-right: 10px;
        padding: 0 10px;
        box-sizing: border-box;
        background-color: transparent;
        outline: none;
        box-shadow: none;
        border: none;
        border: 3px solid #000;
        background-size: 10px;
        .type-option {
          background-color: #FEFFD4;
        }
      }
      .count-select {
        -webkit-appearance:none;
        width: 100px;
        height: 50px;
        font-size: 16px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        color: #000;
        margin-right: 10px;
        padding: 0 10px;
        box-sizing: border-box;
        background-color: transparent;
        outline: none;
        box-shadow: none;
        border: none;
        border: 3px solid #000;
        background-size: 10px;
        .count-option {
          background-color: #FEFFD4;
        }
      }
      .search-input {
        width: 250px;
        height: 50px;
        font-size: 16px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        color: #000;
        padding-left: 10px;
        box-sizing: border-box;
        background-color: transparent;
        outline: none;
        box-shadow: none;
        border: none;
        border: 3px solid #000;
      }
      .search-btn {
        width: 130px;
        height: 50px;
        border-radius: 0;
        background-color: #000;
        color: #FEFFD4;
        font-size: 20px;
        outline: none;
        box-shadow: none;
        font-family: 'BioRhyme' ,'Arvo', serif;
        border: none;
        margin-right: 20px;
        padding: 0 20px;
        cursor: pointer;
      }
    }
  }
 .movie-container {
    position: relative;
    width: 90%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    .total-message {
      position: absolute;
      width: 100%;
      font-size: 22px;
      font-family: 'BioRhyme' ,'Arvo', serif;
      text-align: right;
      padding-top: 40px;
      margin-right: 20px;
      // text-decoration: underline;
      #total-message-text {
        position: absolute;
        right: 5px;
        padding-bottom: 5px;
        box-sizing: border-box;
        border-bottom: 2px solid #000;
        &.none {
          display: none;
        }
      }
    }
    .search-message {
      display: flex;
      margin-top: 180px;
      width: 100%;
      justify-content: center;
      flex-wrap: wrap;
      line-height: 1.3;
      user-select:none;
      &.none{
        display: none;
      }
      img {
        width: 230px;
        height: auto;
        margin-bottom: 5px;
        mix-blend-mode: multiply;
      }
      h4 {
        text-align: center;
        width: 100%;
        font-size: 22px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        margin-bottom: 10px;
      }
    }
    .error {
      display: none;
      &.display {
        display: flex;
        margin-top: 180px;
        width: 100%;
        justify-content: center;
        flex-wrap: wrap;
        line-height: 1.1;
        user-select:none;
      }
      img {
        width: 200px;
        height: auto;
        mix-blend-mode: multiply;
      }
      h4 {
        text-align: center;
        width: 100%;
        font-size: 30px;
        font-family: 'BioRhyme' ,'Arvo', serif;
        margin-bottom: 10px;
      }
      p {
        text-align: center;
        font-family: 'BioRhyme' ,'Arvo', serif;
      }
    }
    .item {
      width: 100%;
      align-items: flex-start;
      margin-top: 130px;
      box-sizing: border-box;
      display: flex;
      flex-wrap: wrap;
      .group {
        padding: 0px 20px 50px 20px;
        display: flex;
        width: 16.666667%;
        min-width: 200px;
        flex-wrap: wrap;
        box-sizing: border-box;
        position: relative;
        &__imgdiv {
          width: 100%;;
          height: 300px;
          background-size: cover;
          border: 3px solid #000;
          background-position: center;
          overflow: hidden;
          padding-top: 20px;
          }
        &__infodiv {
          padding-top: 10px;
          &__title {
            flex: 0 0 100%;
            text-align: left;
            padding-top: 10px;
            color: #000;
            font-family: 'BioRhyme' ,'Arvo', serif;
            font-size: 17px;
            font-weight: 700;
            line-height: 1.4;
          }
          &__year {
            padding-top: 3px;
            font-family: 'BioRhyme' ,'Arvo', serif;
            font-size: 14px;
          }
          &__detail {
            display: flex;
            overflow: hidden;
            align-items: center;
            width: 25px;
            height: 25px;
            background-color: #000;
            border-radius: 30px;
            margin-top: 15px;
            padding-left: 7.5px;
            box-sizing: border-box;
            color: #FEFFD4;
            font-family: 'BioRhyme' ,'Arvo', serif;
            cursor: pointer;
            &:hover{
              width: 85px;
              transition: .4s;
              &::after {
                content: 'detail';
                transition: .3s;
                padding-left: 8px;
              }
            }
          }
        }
      }
    }
    .more {
      width: 100%;
      padding-top: 50px;
      padding-bottom: 100px;
      display: flex;
      justify-content: center;
      .more-btn {
        display: none;
        margin: auto;
        width: 100px;
        height: 50px;
        background-color: #000;
        color: #FEFFD4;
        border: none;
        outline: none;
        box-shadow: none;
        font-family: 'BioRhyme' ,'Arvo', serif;
        font-size: 14px;
        cursor: pointer;
        &.active {
          display: block;
        }
      }
    }
  }
}

.detail {
  position: relative;
  width: 100%;
  height: 100vh;
  overflow: hidden;
  background-color: #000;
  .message {
    position: absolute;
    font-family: 'BioRhyme' ,'Arvo', serif;
    top: 130px;
    width: 100%;
    color: #FEFFD4;
    font-size: 40px;
    text-align: center;
    display: block;
    line-height: 1.3;
    user-select:none;
    &.none {
      display: none;
    }
  }
  .container { 
    position: absolute;
    z-index: 1;
    display: none;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    &.display {
      display : flex;
    }
    .img-wrap {
      width: 400px;
      height: 600px;
      margin-top: 100px;
      background-color: #000;
      background-size: cover;
      background-position: center;
      background-repeat: no-repeat;
      border: 5px solid #FEFFD4;
    }
    .info-wrap {
      margin-top: 100px;
      width: 800px;
      height: 600px;
      margin-left: 50px;
      font-family: 'BioRhyme' ,'Arvo', serif;
      display: flex;
      flex-wrap: wrap;
      align-content: space-between;
      color: #FEFFD4;
      .title-wrap {
        width: 100%;
        .title {
          font-size: 50px;
          font-weight: 700;
          width: 100%;
        }
        .released {
          font-size: 16px;
          margin-top: 20px;
          width: 100%;
        }
        .plot {
          font-size: 14px;
          margin-top: 20px;
          line-height: 1.5;
        }
      }
      .etc-wrap {
        width: 100%;
        .ratings{
          margin-top: 20px;
          font-size: 16px;
          margin-bottom: 40px;
          &-title {
            font-weight: 700;
            font-size: 20px;
          }
          &-info {
            display: flex;
            margin-top: 10px;
            .rating {
              display: flex;
              margin-right: 15px;
              padding-left: 15px;
              border-left: 2px solid #FEFFD4;
              &:first-child {
                padding-left: 0;
                border: none;
              }
              .logo {
                height: 20px;
                margin-right: 10px;
              }
            }
          }
        }
        .actors {
          margin-top: 20px;
          font-size: 16px;
          margin-bottom: 40px;
          &-title {
            font-weight: 700;
            font-size: 20px;
          }
          &-info {
            margin-top: 10px;
            font-size: 16px;
          }
        }
        .director {
          margin-top: 20px;
          font-size: 16px;
          margin-bottom: 40px;
          &-title {
            font-weight: 700;
            font-size: 20px;
          }
          &-info {
            margin-top: 10px;
            font-size: 16px;
          }
        }
        .genre {
          margin-top: 20px;
          font-size: 16px;
          &-title {
            font-weight: 700;
            font-size: 20px;
          }
          &-info {
            margin-top: 10px;
            font-size: 16px;
          }
        }
      }
    }
  }
  .detail-bg {
    background-size: cover;
    background-position: center;
    opacity: 0.2;
    filter: blur(4px);
    width: 100%;
    height: 100vh;
  }
};